import React, { useEffect, useState } from 'react';
import { useParams } from 'react-router';
import { Link } from 'react-router-dom';
import dbPokemon from '../assets/dbPokemon.json'

const CardInfos = () => {

    const { id } = useParams();
    const [pokemon, setPokemon] = useState([])

    useEffect(() => {
        setPokemon(dbPokemon.pokemon[id - 1])
    }, [id])

    console.log(pokemon);

    return (
        <div className="cardInfos">
            <Link to="/react-deploy">
                <h1>Pokédex</h1>
            </Link>
            <div className="infos">
                <span>{pokemon.name}</span>
                <img src={pokemon.img} alt="le pokémon" />
                <span>type: {pokemon.type}</span>
                <span>taille: {pokemon.height} / poids: {pokemon.weight}</span>
                <span>faiblesses: {pokemon.weaknesses}</span>
            </div>
        </div>
    );
};

export default CardInfos;